.About {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin: 400px 0px;
    max-width: 70%;
    font-weight: 300;
}

.About span:first-child {
    font-size: 20px;
}

.About span:last-child {
    display: block;
    margin-top: 10px;
}